import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as emailjs from 'emailjs-com';
@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  Contact_form: FormGroup;
  constructor() { }

  ngOnInit() {
    this.Contact_form = new FormGroup({
      'name': new FormControl(null),
      'email': new FormControl(null, [Validators.required, Validators.email]),
      'mess': new FormControl(null, Validators.required)
    });

    console.log(this.Contact_form);
  }

  Send() {
    const formvalue = this.Contact_form.value;
    const template_params = {
      'from_name': formvalue.name,
      'to_name': formvalue.email,
      'message': formvalue.mess,
   };
    // console.log(this.Contact_form.value);
    emailjs.send('gmail', 'template_r9RFuKqG', template_params, 'user_FOT3TRUjSrG39YMaSA8vu')
    .then((response) => {
       console.log('SUCCESS!', response.status, response.text);
       swal({
        title: `Email was successfully sent`,
        icon: 'success',
      }).then(value => {
        this.Contact_form.reset();
      });
    }, (err) => {
       console.log('FAILED...', err);
       swal({
        title: `Something went wrong. Please try again`,
        icon: 'success',
      });
    });
  }

  f() {
    return this.Contact_form.controls;
  }

}
