import { Component, OnInit, ViewChildren, ElementRef, QueryList, ViewChild } from '@angular/core';
import { WOW } from 'wowjs/dist/wow.min';

@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.scss']
})
export class ProjectComponent implements OnInit {
  projects: any[] = [];
  project_options: any[] = [
    {option_name: 'all'},
    {option_name: 'template'},
    {option_name: 'project'},
  ];
  choose = 'all';
  @ViewChild('project') view_projects: ElementRef;

  constructor() {}

  ngOnInit() {
    new WOW().init();
  }

  Filter(name: string) {
    this.choose = name;
    const arr_item = this.view_projects.nativeElement.getElementsByClassName('item');
    if (this.choose === 'all') {
      for (const item of arr_item) {
        item.classList.remove('hide');
      }
    } else {
      for (const item of arr_item) {
        if (item.getAttribute('data-type') === this.choose) {
          item.classList.remove('hide');
        } else {
          item.classList.add('hide');
        }
      }
    }
  }


}
